body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  overflow: hidden;
}
.map {
  width: 100%;
  height: 80vh;
  position: relative;
}

.location-icon {
  width: 40px;
  height: 40px;
}

.loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.loader img {
  width: 400px;
}

.loader h1 {
  margin-top: -100px;
}

.location-info {
  position: absolute;
  top: 80px;
  right: 40px;
  width: 50%;
  min-height: 10%;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  font-size: 18px;
  color: #fff;
}

.location-info ul {
  list-style-type: none;
  padding: 0;
}

.location-info li {
  padding: 5px 0;
}

.iconloader {
  width: 70px;
  height: 70px;
  position: relative;
  animation-name: loading;
  animation-duration: 2s;
  animation-iteration-count: 3;
}

@keyframes loading {
  0%   {width: 70px; height: 70px;}
  50%   {width:40px; height:40px;}
  100%   {width:70px; height:70px;}
}

.paper-box{
  background-color: black;
}